<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-col cols="12">
      <base-material-card
        color="primary"
        icon="mdi-receipt"
        :title="this.$t('nav.receipt')"
        class="px-5 py-3"
      >
        <v-form
          ref="form"
          lazy-validation
        >
          <v-container class="py-0">
            <v-row>
              <array-text-fields
                :data="api.receipt_text"
                label="Receipt line"
              />
              <v-col
                cols="12"
                md="12"
              >
                <v-spacer />
                <v-btn
                  color="primary"
                  @click="submit"
                >
                  Update
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </base-material-card>
    </v-col>
  </v-container>
</template>

<script>

  export default {
    name: 'Edit',
    components: {
      ArrayTextFields: () => import('@/views/dashboard/components/custom/ArrayTextFields'),
    },
    data: () => ({
      api: {},
    }),
    mounted: function () {
      this.$http.get(`/portal/user/organization`)
        .then(
          (response) => {
            this.api = response.data
            if (this.api.receipt_text === null) { this.api.receipt_text = [] }
          }
        )
    },
    methods: {
      submit () {
        // check if there is still data in the new_line
        if (this.$store.state.changes.error === 'new_line') {
          this.$toasted.global.error(this.$store.state.changes.message)
          return
        }

        if (!this.$refs.form.validate()) {
          return
        }

        this.$http.post(
          `/portal/user/organization/receipt`,
          JSON.stringify({
            receipt_text: this.api.receipt_text,
          })
        )
          .then((response) => {
            this.$store.commit('setChanges', { error: false })
            this.$toasted.global.success(response.data.message)
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },
    },
  }
</script>
